.services {
  padding: 68px calc((100% - 1020px) / 2 + 40px) 51px;
  background: #f2f2f2;
  color: #666d89;
  border-bottom: 6px solid #ffffff;

  &__heading {
    margin: 0;
    font-family: "Lato-Black", "Open Sans", sans-serif;
    font-size: 30px;
    line-height: 0.6;
    font-weight: normal;
  }

  &__description {
    margin: 27px 0 0 0;
    font-family: "Lato-Light", "Open Sans", sans-serif;
    font-size: 18px;
    line-height: 30px;
    text-align: justify;
  }

  &__advantages-list {
    margin-top: 47px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 23px 20px;
  }
}

@media (max-width: 1019px) {
  .services {
    padding: 48px 40px 61px;

    &__advantages-list {
      margin: 38px calc((100vw - 768px)/2) 0;
      grid-template-columns: repeat(2, 1fr);
      justify-content: space-between;
      gap: 43px 20px;
    }
  }
}

@media (max-width: 767px) {
  .services {
    padding: 34px 30px 8px;

    &__description {
      word-spacing: 1px;
    }

    &__advantages-list {
      width: 100%;
      margin: 30px 0 0;
      grid-template-columns: 1fr;
      justify-content: space-between;
      gap: 15px;
    }
  }
}
